hgroup {
	border-bottom: 2px solid black;
	margin-bottom: 5px;
	display: flex;
	flex-flow: column;
	justify-content: space-between;
}

h1 {
	font-weight: 300;
}

h1,
h2 {
	margin: 0;
	padding: 0;
	font-size: 1.4em;
}

h2 {
	font-weight: 200;
}

h3 {
	font-size: 1.75em;
	font-weight: 300;
}

.nav .nav-link,
.nav .nav-link:link,
.nav .nav-link:visited,
.nav .nav-link:hover,
.nav .nav-link:active {
	font-size: 1.5em;
	font-weight: 300;
	background-color: #337ab7;
	color: white;
}

.nav .nav-link.active .nav .nav-link.active:link,
.nav .nav-link.active:visited,
.nav .nav-link.active:hover,
.nav .nav-link.active:active {
	background-color: #24557f;
}

@media screen and (min-width: 768px) {
	hgroup {
		flex-flow: row;
		justify-content: space-between;
	}

	h1 {
		font-weight: 300;
	}

	h1,
	h2 {
		font-size: 2.4em;
	}
}
